import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
/**
 * [state description]
 *  数据储存
 *  -------------------
 * @type {Object}
 */
const state =  {
     fetchLogin:"",
     lang:"1",

     
} 
/**
 * [actions description]
 * ----------------------
 *   fetchLogin 权限处理
 * ----------------------
 * @type {Object}
 */
const actions  = {
    fetchLogin({commit},params){
        let arrays = ["userManagement","users","createUser","queryUserInfo","updateUserInfo","deleteUsers","showRoleInfoUnderUser","assignRoles4User",
                      "role","createRole","queryRoles","updateRole","deleteRoles","showAuthoritiesUnderRole","assignAuthorities4Role",
                      "auth","queryAuthorities",
                      "userCenter","modifyPassword",
                      "goldenWithdraw","queryGoldenWithdraw","insertGoldenCode",
                      "frontUserManagement","users","updateFrontUserInfo","queryFrontUserInfo",
                      "review","queryApplyInfo","approveApply","rejectApply",
                      "marketPrice","updatePrice",
                      "bannerConfig","basicBannerConfig","updateBanner","queryBanners",
                      "assetManagement","account","queryFrontUserAccount",
                      "balance","addUsdg","queryAssetBalance",
                      "topUp","queryFrontUserRecharge",
                      "withdraw","queryFrontUserWithdraw","queryFrontUserWithdrawHistory","firstPass","secondPass","rejectWithdraw",
                      "exchange","queryExchange",
                      "feedback","queryFeedbacks",
                      "notice","queryNotices","queryNoticeContent","updateNotice","deleteNotice","publishNotice","updateNoticeStatus"
        ];

        let array = [];
          arrays.forEach((val,i,arrays) => {
              params.forEach((arr,arrIdx,params) => {
               if(arr.includes(val)){
                   let obj = {};
                   obj[val] = true;
                   array.push(obj);
               }
            });
        });

        let map = {};
        array.forEach(function(element) {
          for(var i in element){
            map[i] = element[i];
          }
        });
        // console.log(map)
    	  commit('FETCH_LOGIN',map)
    },

}
/**
 * [mutations description]
 * -----------------------
 *  state.fetchLogin  
 *  期限数据存入
 * -----------------------
 * @type {Object}
 */
const mutations = {
    FETCH_LOGIN(state,data){
       state.fetchLogin = data;
    },
   

}
/**
 * [getters description]
 * -------------------------
 *  getAuthUrl 权限数据输出
 * -------------------------
 * @type {Object}
 */
const getters = {
    getAuthUrl : state => state.fetchLogin   

}

export default new Vuex.Store({
	  state,
    actions,
	  mutations,
    getters,
})


