import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login'
import Main from '@/components/headMenu'
Vue.use(Router)

export default new Router({
    // mode: 'history',
    routes: [{
        path: '/',
        redirect: '/login',
        hidden: false
    }, {
        path: '*',
        redirect: '/login',
        hidden: false
    }, {
        path: '/login',
        component: Login,
        hidden: false
    }, {
        path: '/headMenu',
        component: Main,
        children: [{
            path: '/home',
            component: function(resolve) {
                require(['../views/home.vue'], resolve)
            }
        }, {
            path: '/amendPetition',
            component: function(resolve) {
                require(['../views/userAdmin/amendPetition.vue'], resolve)
            }
        }, {
            path: '/userList',
            component: function(resolve) {
                require(['../views/userAdmin/userList.vue'], resolve)
            }
        }, {
            path: '/userAssets',
            component: function(resolve) {
                require(['../views/resources/userAssets.vue'], resolve)
            }
        }, {
            path: '/chargeMoney',
            component: function(resolve) {
                require(['../views/resources/chargeMoney.vue'], resolve)
            }
        }, {
            path: '/carryMoney',
            component: function(resolve) {
                require(['../views/resources/carryMoney.vue'], resolve)
            }
        }, {
            path: '/manual',
            component: function(resolve) {
                require(['../views/resources/manual.vue'], resolve)
            }
        }, {
            path: '/exchange',
            component: function(resolve) {
                require(['../views/resources/exchange.vue'], resolve)
            }
        }, {
            path: '/balance',
            component: function(resolve) {
                require(['../views/resources/balance.vue'], resolve)
            }
        }, {
            path: '/goldExtract',
            component: function(resolve) {
                require(['../views/public/goldExtract.vue'], resolve)
            }
        }, {
            path: '/noticeAdmin',
            component: function(resolve) {
                require(['../views/public/noticeAdmin.vue'], resolve)
            }
        },{
            path: '/commonProblems',
            component: function(resolve) {
                require(['../views/public/commonProblems.vue'], resolve)
            }
        }, {
            path: '/marketPrice',
            component: function(resolve) {
                require(['../views/public/marketPrice.vue'], resolve)
            }
        }, {
            path: '/problemFeedback',
            component: function(resolve) {
                require(['../views/public/problemFeedback.vue'], resolve)
            }
        }, {
            path: '/bannerFit',
            component: function(resolve) {
                require(['../views/public/bannerFit.vue'], resolve)
            }
        },{
            path: '/transactionFee',
            component: function(resolve) {
                require(['../views/serviceCharge/transactionFee.vue'], resolve)
            }
        },{
            path: '/withdrawalCharge',
            component: function(resolve) {
                require(['../views/serviceCharge/withdrawalCharge.vue'], resolve)
            }
        },{
            path: '/gradeFee',
            component: function(resolve) {
                require(['../views/serviceCharge/gradeFee.vue'], resolve)
            }
        },{
            path: '/customerService',
            component: function(resolve) {
                require(['../views/systemSetup/customerService.vue'], resolve)
            }
        }, {
            path: '/roleSetting',
            component: function(resolve) {
                require(['../views/systemSetup/roleSetting.vue'], resolve)
            }
        }, {
            path: '/jurisdiction',
            component: function(resolve) {
                require(['../views/systemSetup/jurisdiction.vue'], resolve)
            }
        }, {
            path: '/userCenter',
            component: function(resolve) {
                require(['../views/systemSetup/userCenter.vue'], resolve)
            }
        }]
    }]
})


