<template>
    <div id="login">
       <el-form :model="loginForm" :rules="rulesLogin" ref="loginForm" label-position="left" label-width="0px" class="login-container">
        <h3 class="title">{{$t('login.loginTitle')}}</h3>
        <el-form-item prop="username">
        <el-input type="text" v-model="loginForm.username" auto-complete="off" :placeholder="$t('login.userName')">
            <i slot="prefix" class="iconfont icon-icon-test"></i>
        </el-input>
        </el-form-item>
        <el-form-item prop="password">
        <el-input type="password" v-model="loginForm.password" auto-complete="off" :placeholder="$t('login.passWord')" @keyup.native.enter="handleSubmit2" >
            <i slot="prefix" class="iconfont icon-shiliangzhinengduixiang9"></i>
        </el-input>
        </el-form-item>
        <el-checkbox v-model="checked"  class="remember" @click.native="remember">{{$t('login.remember')}}</el-checkbox>
        <el-form-item style="width:100%;">
        <el-button  style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">{{$t("login.login")}}</el-button>
        </el-form-item>
       </el-form>
    </div>
</template>
<script>
    import {ajax,delatecookie,getcookie, setcookie,} from "../common/js/common.js";
    import {mapActions,mapGetters} from "vuex";
    export default {
        data(){
            var checkName = (rule,value,callback) =>{
                if(!value){
                    return callback(new Error("请输入用户名"))
                }else
                if(value.length<5){
                    return callback(new Error('用户名至少5位'))
                }else{
                    callback()
                }
            };
            var checkPass =(rule,value,callback) =>{
                if(!value){
                    return callback(new Error("请输入密码"))
                }else if(value.length<5){
                    return callback(new Error('密码至少5位'))
                }else{
                    callback()
                }
            };
            return{
              logining:false,
              loginForm:{
                  username:'',
                  password:''
              },
              rulesLogin:{
                  username:[
                      {required: true, validator: checkName,trigger:'blur'}
                  ],
                  password:[
                      {required: true,validator:checkPass,trigger:'blur'}
                  ]
              },
              checked: ""
            }
        },
        /**
         * [computed description]
         *  权限数据读取
         *  ---------------------
         * @type {Object}
         */
        // computed:{
        //   ...mapGetters([
        //     'getAuthUrl'
        //   ])
        // },
        /**
         * [beforeDestroy description]
         *  权限数据查看
         *  --------------------
         * @return {[type]} [description]
         */
        beforeDestroy:function() {
         
        },
        methods:{
           /**
            * [handleSubmit2 description]
            *  登录操作
            *  ---------------------------
            * @return {[type]} [description]
            */
          
            handleSubmit2:function(){
                
                 var name=this.loginForm.username;
                //保存的密码
                var pass=this.loginForm.password;
                 //判断复选框是否被勾选 勾选则调用配置cookie方法
                                if(this.checked==true){
                                    //传入账号名，密码，和保存天数3个参数
                                  this.setCookie(name,pass,7);

                                }
                                if(this.checked ==false){
                                    this.clearCookie();
                                }
                this.$refs.loginForm.validate((valid) => {
                   if(valid){
                       
                     this.logining = true;
                     ajax(this,this.ApiConfig.Login.requestLogin,this.loginForm,(res)=>{
                         if(res.code == 200){
                            console.log(res)
                            sessionStorage.setItem("tokenId",res.data.tokenId)

                            console.log('res.data.tokenId:',res.data.tokenId)

                            sessionStorage.setItem("userPhone",res.data.contact)
                            // console.log(res.data.authUrl)
                            
                            this.fetchLogin(res.data.authUrl)
                            this.$refs['loginForm'].resetFields();
                            this.$router.push({ path:'/headMenu'})
                            // this.$router.push({ path:'/userList'})
                            this.$emit("forMan",res.data);

                         }else {
                            this.logining = false;
                             this.$notify({
                                title:"提示",
                                message:res.message,
                                type:"warning"
                             })
                         }
                     },'POST')
                   }else {
                       return false;
                   }
                })
            },
            //设置cookie
              setCookie(c_name,c_pwd,exdays) {
                var exdate=new Date();//获取时间
                exdate.setTime(exdate.getTime() + 24*60*60*1000*exdays);//保存的天数
                //字符串拼接cookie
                window.document.cookie="userName"+ "=" +c_name+";path=/;expires="+exdate.toGMTString();
                window.document.cookie="userPwd"+"="+c_pwd+";path=/;expires="+exdate.toGMTString();
              },
              //读取cookie
              getCookie:function () {
                if (document.cookie.length>0) {
                  var arr=document.cookie.split('; ');//这里显示的格式需要切割一下自己可输出看下
                  for(var i=0;i<arr.length;i++){
                    var arr2=arr[i].split('=');//再次切割
                    //判断查找相对应的值
                    if(arr2[0]=='userName'){
                      this.loginForm.username=arr2[1];//保存到保存数据的地方
                    }else if(arr2[0]=='userPwd'){
                      this.loginForm.password=arr2[1];
                    }
                  }
                }
              },
               //清除cookie
              clearCookie:function () {
                this.setCookie("","",-1);//修改2值都为空，天数为负1天就好了
              },
              remember(){
                if(this.checked ==false){
                    this.clearCookie();
                }
              },


            /**
             * fetchLogin
             * --------------------
             * 权限状态存入
             */
            ...mapActions([
                'fetchLogin' 
                   
           ])
            
        },
        mounted(){
         this.getCookie()

        }
        
    }
</script>
<style scoped lang="postcss">
     @import '../assets/css/vars';
     #login{
         height: 100%;
         background-image:url(../assets/img/bg.jpg);
        .login-container {
            background-clip: padding-box;
            margin: 0px auto;
            width: 300px;
            height:307px;
            position: absolute;
            top:50%;
            left: 50%;
            margin-left:calc(-300px / 2);
            margin-top:calc(-307px / 2);
            padding: 35px 50px 49px 50px;
            background: $color-primary;
            /* border: 1px solid #eaeaea; */
            /* box-shadow: 0 0 25px #cac6c6; */
            .el-input{
                height: 40px;
            }
            .iconfont{
                font-size: 26px;
                color: #c8c8c8;
                line-height: 40px;
            }
            .el-button{
                height: 40px;
                color: $color-primary;

            }
            .title {
                margin: 0px auto 35px auto;
                text-align: center;
                font-size: 20px;
                color: #fff;
                font-family: $font-primary;
            }
            .remember {
                margin: 0px 0px 35px 0px;
                color: #fff;
                font-size: 20px;
            }
        }
     }
</style>
<style lang="postcss">
  .login-container{
      .el-input__inner{
          height: 40px;
      }
      .el-checkbox__input.is-checked + .el-checkbox__label{
        color: #fff;

      }
      .el-checkbox__input.is-checked .el-checkbox__inner{
        background-color: #bf9864;
        border-color: #fff;
        /* color: #bf9864; */
      }
     
  }
</style>






