<template>
  <div id="app">
    <transition name="fade" mode="out-in">
       <router-view @forMan="userMessage" :authorName="authorName"></router-view>
    </transition>
  </div>
</template>

<script>


export default {
  name: 'App',
  data(){
    return {
      authorName:"",
    }
  },
  methods:{
    userMessage:function(data){
      console.log(data)
      console.log(this.$store.state.fetchLogin)
       // let pow = this.$store.state.fetchLogin
      // sessionStorage.setItem("power",JSON.stringify(this.$store.state.fetchLogin) );
      sessionStorage.setItem("power",JSON.stringify(this.$store.state.fetchLogin) );
      
      this.author = data;
      this.authorName = data.personName
      sessionStorage.setItem('authorName',data.personName)
      console.log(this.author)
    }
  },
  mounted(){
    this.authorName =sessionStorage.getItem('authorName')
  }
}
</script>

<style lang="postcss">
html,body{
  padding: 0px;
  margin: 0px;
  height: 100%;
  width: 100%;
}
#app {
  height: 100%;
  width: 100%;


}
.toolbar{
  margin: -5px 0px 0px;
  .el-form-item {
		margin-bottom: 10px;
    .el-form-item__label{
       vertical-align: middle;
       font-size:14px;
       font-weight: normal;
	     font-stretch: normal;
    }
    .el-form-item__content{
      vertical-align: middle
    }
    .warning{
      background: #bf9764;
      color: #ffffff;
      /* width: 62px; */
    }
    .info{
      background: #f0f0f0
    }
	}
}
.el-table {
  .has-gutter{
     th{
       background-color: #fef9e7;
     }
  }
  .success-row{
    background: #fffdf8
  }
}
.title{
  font-size: 18px;
  font-weight: 600;
  color:#333;
  font-family: "Microsoft Yahei";
}
.el-table th{
    padding:0!important;
    height:39px;
    line-height:39px;
    font-size: 12px;
    color: #bf9764;

   } 

   .page{
      display:inline;
      text-align:center;
      padding:15px;
    }
 
  .el-pagination.is-background .el-pager li:not(.disabled).active{
    background-color: #bf9764;
  }
  .el-pagination.is-background .el-pager li:not(.disabled):hover{
    background-color: #bf9764;
    color: #fff;
  }
   header .headNav .main .content-container .content-wrapper[data-v-dfb4a462]{
  overflow:auto;
}
  
/* 操作栏里的样式 */
.el-button--mini, .el-button--small{
  border: none;
  background-color: transparent;
  color: #bf9764;
  outline: none;
}
.el-button--mini, .el-button--mini.is-round{
      padding: 7px 0px;
}

/* 内容宽度最小值 */
.el-col-24{
  min-width: 1200px;
}
/* 选择日期 */
.el-date-editor.el-input, .el-date-editor.el-input__inner {
    width: 125px;
}
.el-select>.el-input{
  width:168px;
}
.el-input__inner{
    height: 30px;
    line-height: 30px;
    font-size: 12px;

}
.el-button{
    padding: 8px 20px;
    font-size: 12px;
    border: 0;
    
}
/* .el-button:hover{
    color:#bf9864 ;
  } */
.el-table {
  border: 1px solid #f4f4f4; 
}
.el-table--fit{
  border-right: 1;
  border-bottom: 0;
}
.el-table td, .el-table th.is-leaf{
  border-bottom: 1px solid #f4f4f4;
}

table {
  border-color: #f4f4f4;
  border-bottom: 0px;
}

.el-input__icon{
  line-height: 100%;
}
</style>



