<template>
  <header>
      <div class="logo">
         <span>{{$t('headBar.USDG')}}</span>
          <div class="user">
              <i class="iconfont icon-shiliangzhinengduixiang1"></i>
              <span class="username">{{authorName}}</span>
              <span class="loiginOut" @click="exit">{{$t('headBar.Quit')}}</span>
              <p>
                <span @click="switchChinese">中文</span>
                <span>&nbsp;/&nbsp;</span>
                <span @click="switchEnlish">English</span>
              </p>
          </div>
      </div>
      <div class="headNav">
         <el-col :span="24" class="main">
            <aside>
              <el-menu :default-active="$route.path" class="el-menu-vertical-demo" router unique-opened  background-color="#1e2835" text-color="#a2aebf" active-text-color="#ffd04b"  >
                 <el-menu-item index="/home" >
                  <i class="iconfont icon-shouye2"></i>
                   {{$t('navBar.home')}}
                 </el-menu-item>
                 <el-submenu index="/userList" v-if="power.userManagement">
                   <template slot="title" >
                      <i class="iconfont icon-shiliangzhinengduixiang"></i>
                       {{$t('navBar.userAdmin')}}
                   </template>
                   <el-menu-item index="/userList"  v-if="power.queryFrontUserInfo"  class="mainInner" >
                     {{$t('navBar.userList')}}
                   </el-menu-item>
                   <el-menu-item index="/amendPetition" v-if="power.review"  class="mainInner">
                     {{$t('navBar.applyMod')}}
                   </el-menu-item>
                 </el-submenu>
                 <el-submenu index="/userAssets" v-if="power.assetManagement">
                   <template slot="title">
                    <i class="iconfont icon-shiliangzhinengduixiang5"></i>
                    {{$t('navBar.Resources')}} 
                   </template>
                   <el-menu-item index="/userAssets" v-if="power.queryFrontUserAccount"  class="mainInner">
                     {{$t('navBar.userAssets')}} 
                   </el-menu-item>
                   <el-menu-item index="/chargeMoney" v-if="power.queryFrontUserRecharge"  class="mainInner">
                     {{$t('navBar.chargeMoney')}} 
                   </el-menu-item>
                   <el-menu-item index="/carryMoney" v-if="power.withdraw"  class="mainInner">
                     {{$t('navBar.carryMoney')}}
                   </el-menu-item>
                   <!-- <el-menu-item index="/manual"  class="mainInner" >
                     {{$t('navBar.manual')}}
                   </el-menu-item> -->
                   <el-menu-item index="/exchange" v-if="power.queryExchange"  class="mainInner">
                     {{$t('navBar.exchange')}}
                   </el-menu-item>
                   <el-menu-item index="/balance" v-if="power.balance"  class="mainInner">
                     {{$t('navBar.balance')}}
                   </el-menu-item>
                 </el-submenu>
                  <el-menu-item index="/goldExtract" v-if="power.goldenWithdraw">
                    <i class="iconfont icon-huangjin"></i>
                     {{$t('navBar.gold')}}
                  </el-menu-item>
                  <el-menu-item index="/noticeAdmin" v-if="power.notice">
                    <i class="iconfont icon-gonggao"></i>
                     {{$t('navBar.notice')}}
                  </el-menu-item>
                  <el-menu-item index="/commonProblems">
                    <i class="iconfont icon-changjianwenti"></i>
                     {{$t('navBar.commonProblems')}}
                  </el-menu-item>
                  <el-menu-item index="/marketPrice" v-if="power.marketPrice">
                    <i class="iconfont icon-shiliangzhinengduixiang6"></i>
                     {{$t('navBar.market')}}
                  </el-menu-item>
                  <el-menu-item index="/problemFeedback" v-if="power.feedback">
                    <i class="iconfont icon-neibufankui"></i>
                     {{$t('navBar.feedback')}}
                  </el-menu-item>
                  <el-menu-item index="/bannerFit" v-if="power.bannerConfig">
                    <i class="iconfont icon-tupian"></i>
                     {{$t('navBar.banner')}}
                  </el-menu-item>
                  <el-submenu index="/transactionFee" >
                     <template slot="title" >
                        <i class="iconfont icon-qitafeiyong"></i>
                         {{$t('navBar.feeSetting')}}
                     </template>
                     <el-menu-item index="/transactionFee"    class="mainInner" >
                       {{$t('navBar.transactionFee')}}
                     </el-menu-item>
                     <el-menu-item index="/withdrawalCharge"   class="mainInner">
                        {{$t('navBar.WithdrawalCharge')}}
                     </el-menu-item>
                     <el-menu-item index="/gradeFee"   class="mainInner">
                        {{$t('navBar.goldFee')}}
                     </el-menu-item>
                  </el-submenu>




                   <el-submenu index="/customerService" v-if="power.userManagement"  >
                    <template slot="title">
                      <i class="iconfont icon-xitongshezhi1"></i>
                      {{$t('navBar.system')}}
                    </template>
                    <el-menu-item index="/customerService" v-if="power.queryUserInfo"  class="mainInner">
                      {{$t('navBar.customer')}}
                    </el-menu-item>
                    <el-menu-item index="/roleSetting" v-if="power.queryRoles"  class="mainInner">
                      {{$t('navBar.roleSetting')}}
                    </el-menu-item>
                    <el-menu-item index="/jurisdiction" v-if="power.queryAuthorities"  class="mainInner">
                      {{$t('navBar.power')}}
                    </el-menu-item>
                    <el-menu-item index="/userCenter"v-if="power.modifyPassword"  class="mainInner">
                      {{$t('navBar.userCore')}}
                    </el-menu-item>
                  </el-submenu>
              </el-menu>
            </aside>
            <section class="content-container">
              <el-col :span="24" class="content-wrapper">
                  <transition name="fade" mode="out-in">
                     <router-view></router-view>
                  </transition>
              </el-col>
            </section>
         </el-col>
      </div>
  </header>
</template>

<script>
import {delatecookie} from "../common/js/common.js";
export default {
  name: 'HelloWorld',
  props:['authorName'],
  data () {
    return {
      collapsed:false,
      power:"",
    }
  },
  methods: {
      exit:function(){
        this.$router.push({ path:'/login'})
        // delatecookie();
      },
      switchChinese:function(){
        this.$i18n.locale = 'zh';
        this.$store.state.lang = "1";
      },
      switchEnlish:function(){
        this.$i18n.locale = 'en'; 
         this.$store.state.lang = "2";
      }
    },
  mounted:function(){

    let power =sessionStorage.getItem('power')
    this.power = JSON.parse(power)
    // this.power = this.$store.state.fetchLogin;
    // console.log(this.power)

    
   

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="postcss">
    @import '../assets/css/vars.css';
     header{
        width: 100%;
        height: 46px;
        background: $color-primary;
        line-height:46px;
        span{
           color: #ffffff;
           font-size: 16px;
           font-weight:600;
           &:nth-child(1){
             display: inline-block;
             float: left;
             margin-left:15px;
           }
        }
        .user{
          display: inline-block;
          float: right;
          span{
            font-size: 14px;
            font-weight: 500;
          }
          .iconfont{
            color:#fff;
            font-size:14px;
            vertical-align:middle;
          }
          .username{
            margin-left: 8px;
          }
          .loiginOut{
            margin-left: 25px;
            cursor: pointer;
          }
          p{
            display: inline-block;
            margin:0px;
            margin-right:38px;
            cursor: pointer;
          }
        }
        .headNav{
          .main{
              display: flex;
              position:absolute;
              top:46px;
              bottom:0px;
              overflow: hidden;
              overflow-y: auto;
              aside{
                flex: 0 0 230px;
                width: 230px;
                overflow: hidden;
                .el-menu{
                  height: 100%;
                  font-size:14px;
                  i{
                    font-size: 18px;
                    padding-right: 11px;
                  }
                  .el-menu-item{
                    font-size: 15px;
                    padding-left: 46px!important
                  }
                  .el-submenu{
                     font-size: 15px;
                     .el-menu-item{
                       font-size:15px;
                       padding-left: 86px!important;
                     }
                  }
                }
              }

              .icon-huangjin{
                font-size: 20px!important;
              }
              .content-container{
                flex: 1;
                padding: 12px;
                background: #E9EBF1;
                overflow-y: scroll;
                .content-wrapper{
                  background-color: #fff;
                  box-sizing: border-box;
                  width:100%;
                  padding:10px 20px 30px 20px;
                }
              }
          }
        }
     }

     .mainInner{
      height: 35px!important;
      line-height: 35px!important;
     }
</style>
<style lang="postcss">
@import '../assets/css/vars.css';
    .main{
       .el-submenu__title{
           padding-left: 46px!important;
           font-size:15px;
         }
      .el-menu-item, .el-submenu__title{
            height: 45px;
            line-height: 45px;
      }
      .el-table th>.cell{
        text-align: center;
      }
      .el-table .cell{
        text-align: center;
      }
      .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
        border: 1px solid #ddd;
        background-color: #fff;
        color: #a0a0a0;
      }
      li.el-menu-item.mainInner.is-active,li.el-menu-item.is-active{
        color: $color-primary!important;
        background-color:#0f1722!important;

      }
    }
    /* 弹窗按钮颜色 */
     .el-checkbox__input.is-checked+.el-checkbox__label{
        color:#bf9864;
      }
      .el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner{
        background-color:  $color-primary;
        border-color:  $color-primary;
      }
      .el-button--primary{
        background-color:  $color-primary;
        border-color:  $color-primary;
      }
</style>




